$color: #A2290F;

body {
  font-family: 'Roboto', sans-serif;
}

/** Bootstrap Overwrites */
.navbar {
  background-color: #262E47;
}

.btn-primary {
  background-color: $color;
  border-color: $color;

  &:hover {
    background-color: darken($color, 10%);
    border-color: darken($color, 10%);
  }
}